/* Navbar.css */

.navbar {
  display: flex;
  justify-content: center;
  background-color: #282c34;
  padding: 10px 20px;
  color: white;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
}

.navbar-brand {
  font-size: 42px;
  align-items: center;
}

.brand-logo {
  color: white;
  text-decoration: none;
  font-size: 28px;
  align-items: center;
  font-weight: bold;
}

.navbar-toggle {
  display: none;
  font-size: 24px;
  cursor: pointer;
}

.navbar-menu {
  display: flex;
  align-items: center;
  gap: 20px;
}

.navbar-menu a {
  color: white;
  text-decoration: none;
  font-size: 18px;
}

.navbar-menu a:hover {
  text-decoration: underline;
}

.search-form {
  display: flex;
}

.search-form input {
  padding: 5px;
  border: none;
  border-radius: 4px;
}

.search-form button {
  background-color: #61dafb;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

.search-form button:hover {
  background-color: #21a1f1;
}

/* Responsive Design */
@media (max-width: 768px) {
  .navbar-toggle {
    display: block;
  }

  .navbar-menu {
    display: none;
    flex-direction: column;
    width: 100%;
    text-align: center;
    background-color: #282c34;
    position: absolute;
    top: 60px;
    left: 0;
    padding: 10px 0;
  }

  .navbar-menu.open {
    display: flex;
  }

  .navbar-menu a {
    margin: 10px 0;
  }

  .search-form {
    margin-top: 10px;
  }
}
